@import "./../../info-page.scss";


.article-page {
  .body-contacts{
    background-color: #FBFBFB;
  }
  .footer{
    padding-top: 53px;
  }
  .current-page-info {
    &__container {
      .current-page-info__page-name {
        color: $gray;
        &:hover {
        }
        &:active {
        }
        &:focus {
        }
      }
    }
  }

  .article-section {
    margin-top: 110px;
    padding-bottom: 90px;
    .title-block-article {
      &__title {
        @include rale-700-32;
        max-width: 468px;
        line-height: 36px;
      }
      &__date {
        margin-top: 10px;
        @include mont-500-15;
        color: $gray;
      }
    }
    .main-article {
      margin-top: 30px;
      display: flex;
      justify-content: space-between;
      .sidebar-main-article {
        max-width: 260px;
        &__title {
          margin-top: 22px;
          text-align: center;
          @include rale-700-24;
          line-height: 26px;
        }
        &__publications-block {
          margin-top: 19px;
          display: flex;
          flex-direction: column;
          align-items: center;
          gap: 40px;
        }
        .footer-sidebar-main-article {
          margin-top: 55px;
          padding: 43px 20px 29px;
          display: flex;
          flex-direction: column;
          align-items: center;
          background-color: $darkGreen;
          &__title {
            @include rale-800-20;
            line-height: 25px;
            text-align: center;
            max-width: 220px;
            color: $white;
          }
          &__text {
            margin-top: 15px;
            @include rale-600-17;
            color: #EFEFEF;
            text-align: center;
            max-width: 155px;
          }
          .social{
            margin-top: 30px;
            .icon-facebook{
              color: $white;
            }
            .icon-instagram{
              color: $white;
            }
            .icon-youtube{
              color: $white;
            }
          }
        }
        .publication-sidebar-main-article {
          max-width: 220px;
          &__img {
          }
          &__date {
            margin-top: 16px;
            @include mont-500-13;
          }
          &__title {
            @include rale-700-15;
            margin-top: 5px;
          }
          &__link {
            display: block;
            margin-top: 7px;
            &:hover {
            }
            &:active {
            }
            &:focus {
            }
          }
        }
      }
      .content-main-article {
        max-width: 990px;
        .list-content-main-article {
          margin-bottom: 30px;
          &__item {
            position: relative;
            margin-left: 1px;
            padding-left: 29px;
            @include rale-500-16;
            line-height: 27px;
            &:before {
              position: absolute;
              left: 0;
              content: "";
              background-color: $darkGreen;
              border-radius: 50%;
              width: 3px;
              height: 3px;
              left: 0;
              top: 50%;
              transform: translate(0, -50%);
            }
          }
        }
        .img-block-content-main-article {
          &__img {
            margin-bottom: 5px;
          }
        }
        &__title {
          @include rale-700-24;
          line-height: 29px;
          margin-top: 25px;
          max-width: 500px;
          margin-bottom: 22px;
        }
        &__list-title {
          @include rale-700-16;
          margin-top: 24px;
          margin-bottom: 13px;
        }
        &__text {
          margin-top: 14px;
          @include rale-500-16;
          line-height: 25px;
          letter-spacing: 0.01em;
        }
      }
    }
  }
}
